body{
    background-color: rgb(233, 10, 159);
}

.container{
    background-color: rgb(255, 194, 137);
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
}

.image{
    width: 410px;
    margin-top: 50px;
    border: 5px;
    border-color: salmon;
}

@media (max-width: 770px){
    .container{
        margin: 0 10px;
    }
    .image{
        width: 100%;
    }
}